.paginator {
    display: flex;
    justify-content: center;
    align-items: flex-start;
}

.pages {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 18px;
    padding: 5px;
    margin-bottom: 15px;
    animation: show 1s;
}

@keyframes show {
    from {opacity: 0;} to {opacity: 1;}
}

.pageItem {
    background-color: inherit;
    display: inline-block;
    cursor: pointer;
    border-radius: 2px;
    margin-right: 5px;
    border: 1px solid snow;
    padding: 0 3px;
    color: snow;
    transition: all 0.3s ease;
}
.pageItem:hover {
    border: 1px solid snow;
    background-color: snow;
    color: black;
}

.pageItem.selectedPageItem {
    font-weight: 700;
    background-color: snow;
    border: 2px solid #565656;
    color: #565656;
}
.arrowsButton {
    margin: 0 10px;
    cursor: pointer;
}
.arrowsButton img {
    width: 20px;
    height: 20px;
}

@media (max-width: 380px) {
    .pages {
        margin-bottom: 5px;
    }
    .pageItem {
        padding: 3px 6px;
    }
    .arrowsButton img {
        width: 25px;
        height: 25px;
    }
}